    padding: 0;
    margin: 0;
}


body {
    font-family: 'Poppins', sans-serif;
      background-color: white;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

.skip-link {
  position: absolute;
  top: 0;
  left: 0;
  background-color: #ffffff;
  color: #000;
  padding: 0.5rem 1rem;
  z-index: 10000;
  transform: translateY(-100%);
  transition: transform 0.3s ease-in-out;
}

.skip-link:focus {
  transform: translateY(0);
}


/* App Bar Start*/

.app-bar {
    padding: 8px 16px;
    background-color: white;
    display: grid;
    grid-template-columns: auto 1fr auto;
    gap: 10px;
    position: sticky;
    top: 0;
    z-index: 99;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  }
   
  .app-bar .app-bar__menu {
    display: flex;
    align-items: center;
  }
   
  .app-bar .app-bar__menu button {
    background-color: transparent;
    border: none;
    font-size: 18px;
    padding: 8px;
    cursor: pointer;
  }
   
  .app-bar .app-bar__brand {
    display: flex;
    align-items: center;
  }
   
  .app-bar .app-bar__brand h1 {
    color:  #800000;
    text-transform: uppercase;
    font-size: 22px;
    user-select: none;
  }
   
  .app-bar .app-bar__navigation {
    position: absolute;
    top: 50px;
    left: -180px;
    width: 150px;
    transition: all 0.3s;
    padding: 8px;
    background-color: white;
    overflow: hidden;
  }
   
  .app-bar .app-bar__navigation.open {
    left: 0;
  }
   
  .app-bar .app-bar__navigation ul li a {
    display: inline-block;
    text-decoration: none;
    min-width: 44px;
    min-height: 44px;
    line-height: 44px;
    color: #000;
  }

  .app-bar .app-bar__navigation ul li a:hover{
    color:#8e8383;
  }


  #hamburgerButton {
    display: inline-block;
    font-size: 20px;
    justify-content: center;
    align-items: center;
    padding: 10px;
    min-width: 44px;
    min-height: 44px;
  }

/* App Bar End */

/* Hero Section */
.hero {
  width: 100%;
  min-height: 100vh;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  display: flex;
  align-items: center;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
}

.hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}

.hero::after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 30%;
  bottom: 0;
}

.hero .content {
  padding: 2rem 5%;
  max-width: 60rem;
  position: relative;
  z-index: 2;
}

.hero .content h1 {
  font-size: 20px;
  color: #fff;
  text-shadow: 1px 1px 3px rgba(1, 1, 3, 0.5);
  line-height: 1.2;
}

.hero .content p {
  font-size: 1.3rem;
  margin-top: 1rem;
  line-height: 1.3;
  font-weight: 100;
  text-shadow: 1px 1px 3px rgba(1, 1, 3, 0.5);
  margin-bottom: 1.5rem;
}

.hero .content button {
  display: inline-block;
  background-color: #800000;
  color: #fff;
  min-width: 120px;
  min-height: 44px;
  padding: 12px 24px;
  font-size: 17px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  text-align: center;
  text-decoration: none;
  width: auto;
}

.hero .content button:hover {
  background-color: #8e8383;
}

.hero .content button:focus {
  background-color: #fff;
  color: #800000;
  outline: 1px solid #000;
}


/* Main Start */
main {
    flex: 1;
  }
   
  .content {
    margin: 0 auto;
    min-height: 100%;
  }
   
  .content .content__heading {
    font-weight: normal;
  }
/* Main End */


/* Restaurant CSS Start */

.restaurants {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  padding: 18px;
}

/* Card Style */
.restaurant-card {
  background-color: #fff;
  color: #000;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  max-width: 100%;
  height: 100%;
  transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.restaurant-card:hover {
  outline: 2px solid #800000;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
  transform: scale(1.02);
}

.restaurant-card:focus {
  outline: 2px solid #800000;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
  transform: scale(1.02);
}

.restaurant-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.restaurant-info {
  padding: 0.7rem;
}

.restaurant-name {
  font-size: 1.5rem;
}

.restaurant-name a {
  display: inline-block;
  text-decoration: none;
  color: #800000;
  min-width: 44px;
  min-height: 44px;
  line-height: 44px;
}

.restaurant-rating {
  display: flex;
  align-items: center; /* Menyelaraskan item secara vertikal */
  color: #f39c12;
  font-size: 1.2rem;
  gap: 2.5px;
}

.rating-number {
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  color: #f39c12;
}

.rat-name {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  margin-right: 0.2rem;
}


.restaurant-description {
  font-size: 0.9rem;
  color: #333;
}

.restaurant-item {
    width: 100%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    overflow: hidden;
}

.restaurant-item__header {
    position: relative;
}

.restaurant-item__header img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.restaurant-item .restaurant-item__header .restaurant-item__header__rating {
  position: absolute;
  padding: 8px;
  bottom: 20px;
  left: 0;
  display: inline-block;
  background-color: black;
  color: white;
}

.restaurant-item .restaurant-item__header .restaurant-item__header__rating .restaurant-item__header__rating__score {
  margin-left: 10px;
}

.restaurant-item .restaurant-item__content {
  padding: 16px;
}

.restaurant-item .restaurant-item__content h3 {
  margin: 0 0 10px 0;
}

.restaurant-item .restaurant-item__content h3 a {
  color: #db0000;
  text-decoration: none;
}

.restaurant-item .restaurant-item__content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4; /* number of lines to show */
  -webkit-box-orient: vertical;
}

.restaurant-text {
  padding: 10px;
  
}


.restaurant {
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 18px 16px;
}

.restaurant .restaurant__poster{
  width: 50%;
  max-width: 400px;
}

.restaurant .restaurant__info h4{
 margin: 8px 0;
}

.card-text {
  color: #000;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.restaurant-rating {
  display: flex;
  align-items: center; /* Menyelaraskan item secara vertikal */
  color: #f39c12;
  font-size: 1.2rem;
}

.restaurant-city {
  font-size: 1rem;
  color: #000;
  font-weight: 500;
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
}



/* Restaurant CSS End */

/* Restaurant Detail */

.restaurant_detail{
  width: 70%;
  display: flex;
  flex-direction: column;
  margin: 30px auto;
}

.restaurant_detail h1 {
  font-size: 2.5rem;
  font-weight: 800;
  text-decoration: underline;
  color: #800000;
}

.restaurant_detail img {
  width: 100%;
  height: 400px;
  object-fit: cover;
  border: 3px #000 solid ;
}

.restaurant_detail .restaurant_detail_info{
  margin-top: 20px;
}

.restaurant_detail .restaurant_detail_info ul{
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.restaurant_detail .restaurant_detail_info ul li{
  margin-bottom: 10px;
}

.restaurant_detail .restaurant_detail_info h2 {
  text-align: center;
  margin-bottom: 20px;
}

.restaurant_detail .restaurant_detail_review h2 {
  text-align: center;
  margin-top: 20px;
}

.restaurant_detail .restaurant_detail_review ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.restaurant_detail .restaurant_detail_review ul li {
  margin-bottom: 10px;
}

/*
  Like
*/
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}


/* Footer */
footer {
  background-color: #800000;
  color: #fff;
  text-align: center;
  padding: 1rem 0;
  margin-top: 3rem;
}

main{
  display: flex;
  width: 100%;
  flex-direction: column;
}


#jumbotron {
  display: flex;
  width: 100%;
  background-color: rgb(255, 255, 255);
}

#jumbotron img {
  width: 1000px;
  margin: 0px auto;
}

