x;\r\n    min-height: 44px;\r\n    line-height: 44px;\r\n    color: #000;\r\n  }\r\n\r\n  .app-bar .app-bar__navigation ul li a:hover{\r\n    color:#8e8383;\r\n  }\r\n\r\n\r\n  #hamburgerButton {\r\n    display: inline-block;\r\n    font-size: 20px;\r\n    justify-content: center;\r\n    align-items: center;\r\n    padding: 10px;\r\n    min-width: 44px;\r\n    min-height: 44px;\r\n  }\r\n\r\n/* App Bar End */\r\n\r\n/* Hero Section */\r\n.hero {\r\n  width: 100%;\r\n  min-height: 100vh;\r\n  background-image: url('../public/images/hero-image_2.jpg');\r\n  display: flex;\r\n  align-items: center;\r\n  background-size: cover;\r\n  background-position: center;\r\n  background-repeat: no-repeat;\r\n  position: relative;\r\n}\r\n\r\n.hero::before {\r\n  content: \"\";\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  background-color: rgba(0, 0, 0, 0.5);\r\n  z-index: 1;\r\n}\r\n\r\n.hero::after {\r\n  content: \"\";\r\n  display: block;\r\n  position: absolute;\r\n  width: 100%;\r\n  height: 30%;\r\n  bottom: 0;\r\n}\r\n\r\n.hero .content {\r\n  padding: 2rem 5%;\r\n  max-width: 60rem;\r\n  position: relative;\r\n  z-index: 2;\r\n}\r\n\r\n.hero .content h1 {\r\n  font-size: 20px;\r\n  color: #fff;\r\n  text-shadow: 1px 1px 3px rgba(1, 1, 3, 0.5);\r\n  line-height: 1.2;\r\n}\r\n\r\n.hero .content p {\r\n  font-size: 1.3rem;\r\n  margin-top: 1rem;\r\n  line-height: 1.3;\r\n  font-weight: 100;\r\n  text-shadow: 1px 1px 3px rgba(1, 1, 3, 0.5);\r\n  margin-bottom: 1.5rem;\r\n}\r\n\r\n.hero .content button {\r\n  display: inline-block;\r\n  background-color: #800000;\r\n  color: #fff;\r\n  min-width: 120px;\r\n  min-height: 44px;\r\n  padding: 12px 24px;\r\n  font-size: 17px;\r\n  border: none;\r\n  border-radius: 8px;\r\n  cursor: pointer;\r\n  transition: background-color 0.3s ease;\r\n  text-align: center;\r\n  text-decoration: none;\r\n  width: auto;\r\n}\r\n\r\n.hero .content button:hover {\r\n  background-color: #8e8383;\r\n}\